<script>
  import Global from '../../global/global';

  export default {
    name: "person-info",
    data() {
      return {
        personId: "",
        personSrc: "",
        activeName: "first",
        //
        //surgeonData: [],
        personalDetail: {},
        medicalHistory: {},
        healthcheck: {},
        specialitycheck: {},
        appliancecheck: {},
        caseHistory: {},
        examination: {},
        faceExamination: {},
        arthroExamination: {},
        //
        checkDiseaseList: [],
        checkDiseaseListDefaultValue: [],
        diseaseList:[],
        CheckDiseaseListIsChanged:  false,
        //
        pathSuffix: '',
        pathPre: '',
        pathDolphin: '',
        //
        labelPosition: "right",
        labelPositionTop: "top",
        //disabled
        personalDetailIsEdit: true,
        medicalHistoryIsEdit: true,
        healthcheckIsEdit: true,
        specialitycheckIsEdit: true,
        appliancecheckIsEdit: true,
        caseHistoryIsEdit: true,
        examinationIsEdit: true,
        faceExaminationIsEdit: true,
        arthroExaminationIsEdit: true,
        //ISSHow
        watchThreeDPicsIsShow: false,
      }
    },
    mounted() {
      //personId
      this.personId = this.$route.params.personId;
      console.log("personId",this.personId);
      //
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/search/patient/detail/?personId=${this.personId}`,
        headers: {
          'Content-Type': 'application/json',
        },
        xhrFields: {
          withCredentials: true
        },
      }).then((response) => {
        console.log(response);
        if (response.data.code === 1) {
          this.personalDetail = response.data.content[0] || {};
          this.medicalHistory = response.data.content[1] || {};
          this.healthcheck = response.data.content[2] || {};
          this.specialitycheck = response.data.content[3] || {};
          this.appliancecheck = response.data.content[4] || {};
          this.caseHistory = response.data.content[5] || {};
          this.examination = response.data.content[6] || {};
          this.faceExamination = response.data.content[7] || {};
          this.arthroExamination = response.data.content[8] || {};
        }
      });
      //
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/waike/search/classIds?personId=${this.personId}`,
        headers: {
          'Content-Type': 'application/json',
        },
        xhrFields: {
          withCredentials: true
        },
      }).then(response=>{
        if(response.data.code === 1){

          this.checkDiseaseListDefaultValue = this.checkDiseaseList = response.data.content;
        }else{
          this.checkDiseaseList = [];
        }
      });
      //
      this.$ajax({
        method: 'get',
        url: `http://${Global.serverIP}:${Global.port}/bigclass/searchAll`,
        headers: {
          'Content-Type': 'application/json',
        },
        xhrFields: {
          withCredentials: true
        },
      }).then(response=>{
        if(response.data.code === 1){
          this.diseaseList = response.data.content;
        }else if(response.data.code === 0){
          this.diseaseList = [{id:-1,
            className:''}];
        }
        console.log('diseaseList',this.diseaseList);
      });
    },
    methods: {
      toggleEdit(item){
        this.$set(this, item, !this[item]);
      },
      applyEditWaike(item, urlItem, vueItem){
        const data  = this[vueItem];
        console.log(`http://${Global.serverIP}:${Global.port}/waike/edit/${this.personId}/${urlItem}`)
        //
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/edit/${this.personId}/${urlItem}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then((response)=>{
          console.log("");
          console.log(response);
          if(response.data.code === 1){
            alert("");
            this.$set(this, item, true);
          }else if(response.data.code === 0){
            alert("");
          }

        })
      },
      applyEditZhenji(item, urlItem, vueItem){
        const data  = this[vueItem];
        //
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/zhenji/edit/${this.personId}/${urlItem}`,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then((response)=>{
          console.log("");
          console.log(response);
          if(response.data.code === 1){
            alert("");
            this.$set(this, item, true);
          }else if(response.data.code === 0){
            alert("");
          }
        })
      },
      buttonTBack(){
        this.$router.go(-1);
      },
      checkDiseaseListChanged(){
        this.CheckDiseaseListIsChanged = true;
      },
      checkDiseaseListSubmit(){
        const formData = this.checkDiseaseList.map((val) => +val);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/personInfo/execBigClass?personId=${this.personId}`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.$message("",{});
            this.checkDiseaseListDefaultValue = this.checkDiseaseList
          }
        });
      },
      checkDiseaseListRe(){
        this.checkDiseaseList = this.checkDiseaseListDefaultValue;
      },
      handlePathSuffixConfirm(){
        let cache = this.pathSuffix;
        cache.replace('\\','\\\\');
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/external/addFilePath?personId=${this.personId}`,
          data: cache,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields: {
            withCredentials: true
          },
        }).then(response=>{
          if(response.data.code === 1){
            this.$message('');
          }
        })
      },
      watchThreeDPics(){
        this.watchThreeDPicsIsShow = true;
        //localstoragepath
        if( localStorage.getItem('pathPre')){
          this.pathPre = localStorage.getItem('pathPre');
          console.log('pathPre',this.pathPre);
        }else{
          this.$message("");
        }
        if( localStorage.getItem('pathDolphin')){
          this.pathDolphin = localStorage.getItem('pathDolphin');
        }else{
          this.$message("");
        }
        //ajax
        //
      }
    }
  }
</script>

<style lang="scss">
  @import "../../scss/font";
  @import "../../scss/style";

  * {
    margin: 0;
    border: 0;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }

  .personInfo-wrap {
    overflow-y: scroll;
    position: relative;
    min-height: 60%;
    max-height: 99%;
    min-width: 960px;
    max-width:960px;
    /*top: 0;
    bottom: 0;
    left: 50%;*/
    /*-webkit-transform: translate(-50%);
    -moz-transform: translate(-50%);
    -ms-transform: translate(-50%);
    -o-transform: translate(-50%);
    transform: translate(-50%);*/
    background-color: #E8E8E8;
    -webkit-border-radius: $border-radius-big;
    -moz-border-radius: $border-radius-big;
    border-radius: $border-radius-big;
    padding: 1rem 3rem 1rem 1rem;
    .el-input.is-disabled input{
      cursor: text;
      user-select: text;
      color: black;
      background: transparent;
      border-bottom: 0.5px black solid;
    }
    .edit{
      float:right;
    }
    .applyEdit{
      display: block;
      margin: 0.5rem auto;
    }

    .second-tag{
      margin-left: 0.5rem;
    }
    .button-back {
      position: fixed;
      top: 1rem;
      right:1rem;
    }
    .checkDiseaseButton{
      text-align: center;
    }
    .checkDiseaseButton button{
      margin: 0 auto;
      display: inline-block;
      font-size:0.8rem;
    }
    .personInfo-diseaseList{
      margin: 0.1rem;
    }
    .typein-path{
      margin: 1rem 0;
    }
  }
</style>



