<template>
  <div class="person-detail-wrap">
    <input-box :title="tagTitleFirst" id="presentInfo" v-model="postData.presentInfo"> </input-box>
    <input-box :title="tagTitleSecond" id="historyInfo" v-model="postData.historyInfo"> </input-box>
    <input-box :title="tagTitleThird" id="systemIndex" v-model="postData.systemIndex"> </input-box>
    <input-box :title="tagTitleFourth" id="systemIndexA" v-model="postData.systemIndexA"> </input-box>
    <input-box :title="tagTitleFifth" id="systemIndexB" v-model="postData.systemIndexB"> </input-box>
    <input-box :title="tagTitleSixth" id="systemIndexC" v-model="postData.systemIndexC"> </input-box>
    <input-box :title="tagTitleSeventh" id="systemIndexD" v-model="postData.systemIndexD"> </input-box>
    <input-box :title="tagTitleEighth" id="systemIndexE" v-model="postData.systemIndexE"> </input-box>
    <input-box :title="tagTitleNinth" id="systemIndexF" v-model="postData.systemIndexF"> </input-box>
    <input-box :title="tagTitleTenth" id="systemIndexG" v-model="postData.systemIndexG"> </input-box>
    <input-box :title="tagTitleEleventh" id="systemIndexH" v-model="postData.systemIndexH"> </input-box>
    <input-box :title="tagTitleTwelfth" id="personalHistory" v-model="postData.personalHistory"> </input-box>
    <input-box :title="tagTitleThirteenth" id="familyHistory" v-model="postData.familyHistory"> </input-box>
    <div class="submit-button" @click="_submit" ></div>
  </div>
</template>

<script>
  import InputBox from '../../../assets/inputBox/inputBox'
  import Global from '../../../global/global';
  export default {
    name: "medical-history",
    data(){
      return{
        tagTitleFirst: "",
        tagTitleSecond: "",
        tagTitleThird: "",
        tagTitleFourth: "",
        tagTitleFifth: "",
        tagTitleSixth: "",
        tagTitleSeventh: "",
        tagTitleEighth: "",
        tagTitleNinth: "",
        tagTitleTenth: "",
        tagTitleEleventh: "",
        tagTitleTwelfth: "",
        tagTitleThirteenth: "",
        postData: {
          presentInfo:'',
          historyInfo:'',
          systemIndex:'',
          systemIndexA:'',
          systemIndexB:'',
          systemIndexC:'',
          systemIndexD:'',
          systemIndexE:'',
          systemIndexF:'',
          systemIndexG:'',
          systemIndexH:'',
          personalHistory:'',
          familyHistory:'',
        },
        personId: '',
      };
    },
    components:{
      InputBox,
    },
    created(){
      if(this.$route.query.personId){
        this.personId = this.$route.query.personId;
      }else{
        alert("");
        this.$router.push({path:`/typein/personalDetail`});
      }
    },
    methods:{
      _submit(event) {
        event.preventDefault();
        const formData = JSON.stringify(this.postData);
        this.$ajax({
          method: 'post',
          url: `http://${Global.serverIP}:${Global.port}/waike/add/${this.personId}/medicalHistory`,
          data: formData,
          headers: {
            'Content-Type': 'application/json',
          },
          xhrFields:{
            withCredentials:true
          },
        }).then((response) => {
          console.log(response);
          this.$router.push({path:`/typein/healthcheck?personId=${this.personId}`});
        })

      }
    }

  }
</script>


<style scoped lang="scss">
  @import "../../../scss/style";

  .submit-button{
    @extend %submit-button;
  }
</style>



